import React, { useMemo } from 'react';
import { useRecoilValue } from 'recoil';
import { useDebug } from '../../hooks/basic/useDebug';
import assoInfoAtom from '../../recoil/atom/association/assoInfoAtom';
import SideAccordions from './SideMenu/SideAccordions';

const SideMenuAssociation = () => {
  const menu = useMemo(() => {
    return [
      {
        name: 'associtation-home',
        title: '홈',
        path: '/association/home',
        src: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/side_menu/home.png',
      },
      {
        name: 'associtation-levelup',
        title: '승급 이력',
        path: '/association/levelup/list',
        src: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/side_menu/levelup.png',
      },
      {
        name: 'associtation-dojang',
        title: '도장 현황',
        path: '/association/dojang/list',
        src: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/side_menu/dojang.png',
      },
      {
        name: 'associtation-student',
        title: '수련생 현황',
        path: '/',
        src: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/side_menu/student.png',
      },
      {
        name: 'associtation-notice',
        title: '협회 공지',
        path: '/',
        src: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/side_menu/notice.png',
      },
      {
        name: 'associtation-schedule',
        title: '협회 일정',
        path: '/',
        src: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/side_menu/attendance.png',
      },
    ];
  }, []);

  const assoInfo = useRecoilValue(assoInfoAtom)
  // useDebug(assoInfo)

  return (
    <div className="side-container">
      <div className="ASside-profile">
        <div className="ASside-profile-img flex flex-col">
          <img 
            src={`https://tkdw-img.s3.ap-northeast-2.amazonaws.com/Association/${assoInfo?.eng_name}_LOGO.png`}
          />
          <p className='mb-0 font-bold text-[17px]'>{assoInfo.full_name}</p>
        </div>
      </div>
      <div className="mt-[20px]">
        <SideAccordions menu={menu} />
      </div>
    </div>
  );
};

export default SideMenuAssociation;
