import authApi from "../authApi"
import { FormDataConfig } from "../config"

export const getSabumInfo = async (dojangId, sabumId) => {
  const res = await authApi.get(`sabum/sabumread/${dojangId}/${sabumId}`)
  return res.data
}

export const getSabumList = async (dojangId, classId) => {
  const res = await authApi.get(`sabum/sabumreadbyclass/${dojangId}/${classId}`)
  return res.data
}

export const getSabumSpecInfo = async (dojangId, sabumId) => {
  const res = await authApi.get(`sabum/sabumreaddetail/${dojangId}/${sabumId}`)
  return res.data
}

export const outSabumToClass = async (dojangId, sabumId) => {
  const res = await authApi.delete(`sabum/sabumdojangdisconnect/${dojangId}/${sabumId}`)
  return res
}

export const addClassToSabum = async (dojangId, sabumId, body) => {
  const res = await authApi.post(`sabum/sabumclassconnect/${dojangId}/${sabumId}`, body)
  return res
}

export const createSabumInfo = async (body) => {
  const res = await authApi.post('sabum/sabumcreate', body, FormDataConfig)
  return res
}

export const updateSabumInfo = async (sabumId, body) => {
  const res = await authApi.post(`sabum/sabumupdate/${sabumId}`, body, FormDataConfig)
  return res
}

export const getAccountInfoSabum = async (userId) => {
  const res = await authApi.get(`sabum/sabumreadall/${userId}`)
  return res.data
}

// 사범 퇴소
export const resignSabum = async (dojangId, sabumId) => {
  const res = await authApi.delete(`sabum/sabumdojangdisconnect/${dojangId}/${sabumId}`)
  return res;
}

