import React, { useMemo } from 'react';
import SideAccordions from './SideMenu/SideAccordions';
import DojangSelect from '../common/Select/DojangSelect';
import { useRecoilValue } from 'recoil';
import userInfoAtom from '../../recoil/atom/login/userInfoAtom';
import { useDebug } from '../../hooks/basic/useDebug';
import { useQuery } from '@tanstack/react-query';
import { getAccountInfo } from '../../api/Kwanjang/kwanjang';
import { getAccountInfoSabum } from '../../api/Sabum/sabum';
import ProfileImage from '../UI/Atoms/Profile/ProfileImage';
import { getFullname } from '../../utils/render/getFullname';

const SideMenu = () => {
  const kwanjangMenu = useMemo(() => {
    return [
      {
        name: 'home',
        title: '홈',
        path: '/home',
        src: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/side_menu/home.png',
      },
      {
        name: 'invite',
        title: '초대',
        src: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/side_menu/invite.png',
        sub: [
          { name: 'invite-send', title: '초대 하기', path: '/invite/send' },
          { name: 'invite-manage', title: '초대장 관리', path: '/invite/manage' },
        ],
      },
      {
        name: 'manage',
        title: '도장 관리',
        src: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/side_menu/dojang.png',
        sub: [
          { name: 'manage-dojang', title: '도장 정보', path: '/manage/dojang' },
          { name: 'manage-sabum', title: '사범 관리', path: '/manage/sabum' },
          { name: 'manage-class', title: '반 관리', path: '/manage/class' },
          { name: 'manage-level', title: '차량 관리', path: '/manage/bus' },
          { name: 'manage-bus', title: '급/띠 체계', path: '/manage/level' },
        ],
      },
      {
        name: 'manage-student',
        title: '수련생 관리',
        path: '/manage/student',
        src: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/side_menu/student.png',
      },
      {
        name: 'attendance',
        title: '출결',
        src: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/side_menu/attendance.png',
        sub: [
          { name: 'attendance-resist', title: '등록', path: '' },
          { name: 'attendance-status', title: '조회', path: '/attendance/status' },
        ],
      },
      {
        name: 'bill',
        title: '납부',
        src: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/side_menu/bill.png',
        sub: [
          { name: 'bill-resist', title: '조회/등록', path: '/bill/resist' },
          { name: 'bill-status', title: '현황', path: '' },
        ],
      },
      {
        name: 'levelup',
        title: '승급',
        src: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/side_menu/levelup.png',
        sub: [
          { name: 'levelup-resist', title: '등록', path: '/levelup/resist' },
          { name: 'levelup-history', title: '이력', path: '/levelup/history' },
        ],
      },
      {
        name: 'payment',
        title: '결제',
        path: '/payment',
        src: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/side_menu/payment.png',
      },
      {
        name: 'pid',
        title: '가맹 등록',
        path: '/pid',
        src: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/side_menu/payment.png',
      },
      {
        name: 'app',
        title: '앱',
        path: '/',
        src: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/side_menu/app.png',
      },
      {
        name: 'profile',
        title: '내 정보',
        path: '/mypage',
        src: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/side_menu/profile.png',
      },
    ];
  }, []);

  const sabumMenu = useMemo(() => {
    return [
      {
        name: 'home',
        title: '홈',
        path: '/home',
        src: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/side_menu/home.png',
      },
      {
        name: 'invite',
        title: '초대',
        src: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/side_menu/invite.png',
        sub: [
          { name: 'invite-send', title: '초대 하기', path: '/invite/send' },
          { name: 'invite-manage', title: '초대장 관리', path: '/invite/manage' },
        ],
      },
      {
        name: 'manage',
        title: '도장 관리',
        src: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/side_menu/dojang.png',
        sub: [
          { name: 'manage-class', title: '반 관리', path: '/manage/class' },
          { name: 'manage-level', title: '차량 관리', path: '/manage/bus' },
          { name: 'manage-bus', title: '급/띠 체계', path: '/manage/level' },
        ],
      },
      {
        name: 'manage-student',
        title: '수련생 관리',
        path: '/manage/student',
        src: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/side_menu/student.png',
      },
      {
        name: 'attendance',
        title: '출결',
        src: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/side_menu/attendance.png',
        sub: [
          { name: 'attendance-resist', title: '등록', path: '' },
          { name: 'attendance-status', title: '조회', path: '/attendance/status' },
        ],
      },
      {
        name: 'app',
        title: '앱',
        path: '/',
        src: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/side_menu/app.png',
      },
      {
        name: 'profile',
        title: '내 정보',
        path: '/mypage',
        src: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/side_menu/profile.png',
      },
    ];
  }, []);

  const userInfo = useRecoilValue(userInfoAtom);
  // useDebug(userInfo)

  // query
  const { data } = useQuery(
    ['getMyAccountInfo', userInfo?.id],
    () => {
      if (userInfo?.user_role === 'KWANJANG') {
        return getAccountInfo(userInfo?.id);
      }
      if (userInfo?.user_role === 'SABUM') {
        return getAccountInfoSabum(userInfo?.id);
      }
    },
    {
      enabled: !!userInfo?.id,
    },
  );

  // useDebug(data)

  return (
    <>
      <div className="side-container">
        <div className="side-profile">
          <div className="side-profile-img flex flex-col">
            <ProfileImage size={80} urlKey={data?.photo_url} />
            <p>
              {getFullname(data?.last_name, data?.first_name)}
              {userInfo?.user_role === 'KWANJANG' ? ' 관장님' : ' 사범님'}
            </p>
          </div>
          <div className="side-profile-btn">
            <DojangSelect width={'100%'} />
          </div>
        </div>
        <div className="mt-[10px]">
          <SideAccordions menu={userInfo?.user_role === 'KWANJANG' ? kwanjangMenu : sabumMenu} />
        </div>
      </div>
    </>
  );
};

export default SideMenu;
