import authApi from "../authApi"
import { FormDataConfig } from "../config"

export const getKwanjangInfo = async (kjId) => {
  const res = await authApi.get(`kwanjang/kwanjangread/${kjId}`)
  return res.data
}

export const createKwanjangInfo = async (body) => {
  const res = await authApi.post('kwanjang/kwanjangcreate', body, FormDataConfig)
  return res
}

export const updateKwanjangInfo = async (KwanjangId, body) => {
  const res = await authApi.post(`kwanjang/kwanjangupdate/${KwanjangId}`, body, FormDataConfig)
  return res
}

export const getAccountInfo = async (userId) => {
  const res = await authApi.get(`kwanjang/kwanjangreadall/${userId}`)
  return res.data
}







