import React, { useState } from 'react';
import { Select as MUISelect } from '@mui/material';
import MenuItem from '@mui/material/MenuItem';
import dojangInfoAtom from '../../../recoil/atom/dojangInfoAtom';
import { useRecoilValue, useSetRecoilState } from 'recoil';
import dojangIdAtom from '../../../recoil/atom/dojangIdAtom';
import dojangListAtom from '../../../recoil/atom/dojangListAtom';
import { useDebug } from '../../../hooks/basic/useDebug';
import LoadingModal from '../../UI/Atoms/Spinner/LoadingModal';

const selectItemStyle = {
  width: '100%',
  position: 'relative',
  display: 'flex',
  alignItems: 'center',
  '&:hover': {
    backgroundColor: '#f3f3f3',
    fontWeight: 'bold',
    '&:before': {
      content: '""',
      backgroundImage: 'url(https://tkdw-img.s3.ap-northeast-2.amazonaws.com/check_select.png)',
      width: '15px',
      height: '15px',
      position: 'absolute',
      right: '10px',
    },
  },
  '&:focus': {
    fontWeight: 'bold',
    '&:before': {
      content: '""',
      backgroundImage: 'url(https://tkdw-img.s3.ap-northeast-2.amazonaws.com/check_select.png)',
      width: '15px',
      height: '15px',
      position: 'absolute',
      right: '10px',
    },
  },
};

const DojangSelect = ({ width, ...props }) => {
  const [loading, setLoading] = useState(false)

  // recoil
  const dojangList = useRecoilValue(dojangListAtom)
  const setDojangId = useSetRecoilState(dojangIdAtom)
  const dojangId = useRecoilValue(dojangIdAtom)
  const setDojangInfo = useSetRecoilState(dojangInfoAtom)

  // handler
  const handleChange = e => {
    setLoading(true)
    setDojangId(e.target.value)
    let temp = dojangList?.filter((v) => v.id == e.target.value)[0]
    setDojangInfo(temp)
    setTimeout(() => setLoading(false), 1000)
  }

  return (
    <>
    { dojangList && dojangId &&
      <>
        <MUISelect
          sx={{ width: `${width || '140px'}`, height: '40px', fontSize: '15px', textAlign: 'center' }}
          size="small"
          value={dojangId}
          onChange={handleChange}
          {...props}
        >
          { dojangList && 
            React.Children.toArray( 
              dojangList?.map(({id, name}) =>
                <MenuItem value={id} sx={selectItemStyle}
                >
                  {name}
                </MenuItem>
              )
            )}
        </MUISelect>
        { loading && <LoadingModal />}
      </>
    }
    </>
  );
};

export default DojangSelect;
