import React, { useState } from 'react';
import { styled } from '@mui/material/styles';
import ArrowForwardIosSharpIcon from '@mui/icons-material/ArrowForwardIosSharp';
import MuiAccordion from '@mui/material/Accordion';
import MuiAccordionSummary from '@mui/material/AccordionSummary';
import MuiAccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import { useLocation, useNavigate } from 'react-router-dom';
import { MenuItem } from '@mui/material';
import { useDebug } from '../../../hooks/basic/useDebug';
import { useLogout } from '../../../hooks/logout/useLogout';

const Accordion = styled((props) => (
  <MuiAccordion disableGutters elevation={0} square {...props} />
))(({ theme }) => ({
  // border: '1px solid #eeeeee',
  // '&:not(:last-child)': {
  //   borderBottom: 0,
  // },
  '&:before': {
    display: 'none',
  },
  '& .Mui-expanded': {
    color: theme.palette.primary.main,
    backgroundColor: '#F5F7F8',
    fontWeight: 'bold',
    borderRadius: '5px',
  },
  '&.Mui-disabled': {
    backgroundColor: '#ffffff',
  },
  
}));

const AccordionSummary = styled(({ icon, ...props }) => (
  <MuiAccordionSummary
    expandIcon={
      icon && <ArrowForwardIosSharpIcon sx={{ fontSize: '0.9rem', transform: 'rotate(90deg)' }} />
    }
    {...props}
  />
))(({ theme }) => ({
  backgroundColor: '#FFFFFFF',
  flexDirection: 'row',
  width: '100%',
  minHeight: '46px',
  '& .MuiTypography-root': {
    marginLeft: '5px',
  },
  '& .MuiAccordionSummary-content': {
    margin: '0px',
    justifyContent: 'flex-start',
    alignItems: 'center',
  },
  '& .MuiAccordionSummary-expandIconWrapper.Mui-expanded': {
    transform: 'rotate(180deg)',
  },
}));

const AccordionDetails = styled(MuiAccordionDetails)(({ theme }) => ({
  // padding: theme.spacing(2),
  // border: '1px solid #eeeeee',
  paddingTop: '5px',
  paddingBottom: '5px',
  '& .MuiMenuItem-root': {
    fontSize: '15px',
    '&:hover': {
      borderRadius: '5px',
    },
    '&:active': {
      backgroundColor: '#D2DAF5',
      borderRadius: '5px',
    },
    '&:focus': {
      backgroundColor: '#D2DAF5',
      borderRadius: '5px',
      color: theme.palette.primary.main,
      fontWeight: 'bold',
    },
  },
  '& .MuiTouchRipple-ripple': {
    '&:hover': {
      backgroundColor: '#D2DAF5',
    },
  },
}));

const SideAccordions = ({ menu }) => {
  const currPath = useLocation().pathname;
  const navigate = useNavigate();
  const [expanded, setExpanded] = useState('');

  const handleChange = (panel) => (event, newExpanded) => {
    setExpanded(newExpanded ? panel : false);
  };


  return (
    <>
      {React.Children.toArray(
        menu?.map((item) => (
          <>
            {item?.path && (
              
                <AccordionSummary 
                  onClick={() => {
                    setExpanded('');
                    navigate(item?.path);
                  }}
                  disabled={item?.path == '/'}
                  className={
                    item.path === currPath ? 'bg-[#f5f7f8] rounded-[5px] text-[#2F5DF2] font-bold' : ''
                  }
                >
                  <img src={item?.src} className="h-[20px]" />
                  <Typography>{item?.title}</Typography>
                </AccordionSummary>
              
            )}
            {item?.sub && (
              <Accordion expanded={expanded === item?.name} onChange={handleChange(item?.name)}>
                <AccordionSummary icon sx={{ fontSize: 'bold' }}>
                  <img src={item?.src} className="h-[20px]" />
                  <Typography>{item?.title}</Typography>
                </AccordionSummary>
                <AccordionDetails>
                  {item?.sub &&
                    React.Children.toArray(
                      item?.sub.map((tem) => (
                        <MenuItem
                          className={
                            tem.path === currPath ? 'bg-[#f5f7f8] rounded-[5px] text-[#2F5DF2] font-bold' : ''
                          }
                          onClick={() => navigate(tem.path)}
                          disabled={!tem?.path}
                        >
                          {tem?.title}
                        </MenuItem>
                      )),
                    )}
                </AccordionDetails>
              </Accordion>
            )}
          </>
        )),
      )}

      <Accordion onChange={() => navigate('/logout')}>
        <AccordionSummary>
          <img src='https://tkdw-img.s3.ap-northeast-2.amazonaws.com/side_menu/logout.png' className="h-[20px]" />
          <Typography>로그아웃</Typography>
        </AccordionSummary>
      </Accordion>
    </>
  );
};

export default SideAccordions;
