import React from 'react'
import { useNavigate } from 'react-router-dom';

const Header = ({ temporary, children }) => {
  const navigate = useNavigate()

  const setImg = {
    logo: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/header/TKW_LOGO.png',
    option: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/header/option.png',
    bell: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/header/bell.png',
  };

  return (
    <>
      <div className="header-container">
        <div className="header-logo">
          <a href="/">
            <img src={setImg.logo} alt="logo"
              className='w-[160px]'
            />
          </a>
        </div>

        <div className="header-right">
          { !temporary && 
            <img src={setImg.option} onClick={() => navigate('/mypage')} alt="설정" className="w-[20px] h-[20px]" /> 
          }
          <img src={setImg.bell} alt="알림" className="w-[20px] h-[20px]" />
        </div>
      </div>
    </>
  );
}

export default Header