import { Navigate, Outlet } from 'react-router-dom';
import { useRecoilValue } from 'recoil';
import userInfoAtom from '../../../recoil/atom/login/userInfoAtom';

const IsLinked = () => {
  const { user_role, is_linked } = useRecoilValue(userInfoAtom)
  
  if ( user_role == 'KWANJANG' && is_linked == 0 ) {
    return <Navigate to="/account/kwanjang/resist" />
  }
  
  if ( user_role == 'SABUM' && is_linked == 0 ) {
    return <Navigate to="/account/sabum/resist" />
  }

  return  <Outlet /> 
};

export default IsLinked