import React from 'react';
import PhoneAndroidIcon from '@mui/icons-material/PhoneAndroid';
import { Link } from '@mui/material';

const Article = () => {
  const src = {
    banner: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/article/banner.png',
    google: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/app/googlestore.png',
    apple: 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/app/appstore.png',
    download: {
      play: 'https://play.google.com/store',
      app: 'https://www.apple.com/kr/app-store/',
    },
  };

  return (
    <>
      <div className="flex flex-col">
        <div className="article-container sticky">
          <div className="article-imgbox text-center py-[15px]">
            <img src={src.banner} />
          </div>
          <div className="article-textbox text-start text-[#333333] tracking-normal px-[10px]">
            <div className="text-[18px] font-bold my-[20px]">
              태권월드
              <br /> 문의접수 안내
            </div>
            <div className="mb-[5px] text-[14px] text-[#2F5DF2] font-medium">이메일 문의</div>
            <div className="mb-[5px] text-[12px] tracking-wider">info@sportsent.org</div>
            <div className="w-[125px] h-[1px] bg-[#000000] mb-[20px]"></div>
            <div className="text-[13px]">
              담당자가 확인 후 이메일로
              <br />
              답변드리겠습니다.
            </div>
          </div>
        </div>
        <div className="article-container flex h-[100px]">
          <div className="w-[80px] h-[80px] text-center p-[10px]">
            <PhoneAndroidIcon className="w-[60px] h-[60px]" />
          </div>
          <div>
            <p className="text-[12px] text-[#333333] mb-[5px]">
              모바일 버전 <br /> 다운로드 하러 가기
            </p>
            <div className="flex gap-[10px]">
              <Link className="cursor-pointer" href={src.download.play} target="_blank">
                <img src={src.google} />
              </Link>
              <Link className="cursor-pointer" href={src.download.app} target="_blank">
                <img src={src.apple} />
              </Link>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Article;
